.header {
    margin-bottom: 6rem;
}

.container {
    padding: 0 1rem;
}

.header-nav {
    display: flex;
    height: 64px;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    margin-bottom: 5rem;
    top: 0;
    padding: 0 1rem;
    padding-top: 3rem;
    z-index: 109;
}

.logo {
    max-width: 64px;
    cursor: pointer;
    top: 0;
}

.burger {
    width: 50px;
    height: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    
}

.burger div {
    width: 100%;
    height: 3px;
    background: white;
}

.title {
    font-size: 60px;
    margin-bottom: 3rem;
    transition: 150ms;
}

.description {
    font-size: 20px;
    margin-bottom: 5rem;
}

.nav-container {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    width: 300px;
    background: rgba(0, 0, 0, 0.85);
    font-weight: 300;
    font-size: 18px;
    transform: translateX(100%);
    z-index: 100;
    padding: 0 1rem;
    padding-top: 6rem;
    transition: 300ms ease-in-out;
}

.nav-container.is-open {
    transform: translate(0);
}

.close {
    position: absolute;
    cursor: pointer;
    top: 2rem;
    right: 0;
}

.nav-container ul {
    display: flex;
    flex-direction: column;
    gap: 40px;
}

.nav-container ul li {
    cursor: pointer;
}

.nav-container ul li:hover {
    color: rgb(255, 86, 86);
}

.close-shoes-overlay {
    position: absolute;
    color: white;
    width: 50px;
    height: 50px;
    font-size: 15px;
    top: 2rem;
    right: 0;
    cursor: pointer;
    display: none;
    justify-content: center;
    align-items: center;
    border: .5px solid white;
}

.shoes-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.75);
    transform: translateX(-100%);
    transition: 300ms ease-in-out;
    z-index: 120;
}

.shoes-display {
    position: relative;
    cursor: pointer;
}

.shoes-display:hover .shoes-display-number {
    opacity: 1;
}

.shoes-display:hover .shoes-display-image {
    transform: translateY(-5px);
    opacity: .5;
}

.shoes-overlay-container {
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    overflow-y: scroll;
    padding-top: 3rem;
}

.shoes-display-image {
    max-width: 120px;
    transition: 100ms ease-in-out;
}

.shoes-display:nth-child(even) .shoes-display-image {
    transform: scaleX(-1);
}

.shoes-display:nth-child(even):hover .shoes-display-image {
    transform: translateY(-5px )scaleX(-1);
}

.number-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.shoes-display-number {
    font-size: 1.5rem;
    font-weight: 900;
    opacity: 0;
    transition: 100ms;
}

.close-shoes-overlay {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 300ms ease-in-out;
    position: absolute;
    pointer-events: none;
    opacity: 0;
    top: 0;
}

.shoes-overlay.is-active {
    transform: translateX(0);
}

.shoes-overlay.is-active .close-shoes-overlay {
    pointer-events: all;
    opacity: 1;
}

.close-shoes-overlay:hover {
    color:rgb(255, 86, 86);
    border: .5px solid rgb(255, 86, 86);
}

@media screen and (min-width: 1024px){

    .nav-container {
        position: relative;
        width: 100%;
        background: transparent;
        transform: translate(0);
        padding-top: 0;
    }

    .header-nav {
        max-width: 1400px;
        margin: 0 auto;
        padding-left: 0;
        padding-right: 0;
        margin-bottom: 10rem;
    }

    .nav-container ul {
        flex-direction: row;
        justify-content: flex-end;
    }

    .burger {
        display: none;
    }

    .close {
        display: none;
    }

    .title {
        font-size: 150px;
        line-height: 140px;
    }

    .container {
        max-width: 1200px;
        margin: 0 auto;
    }

    .description {
        margin-bottom: 10rem;
    }

    .start-button {
        font-size: 1.5rem;
    }

    .shoes-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 25rem;
        bottom: 0;
        transform: translateX(-100%);
        transition: 300ms ease-in-out;
        z-index: 120;
    }

    .close-shoes-overlay {
    right: -5rem;
    top: 2rem;
    }
}

.start-button {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 320px;
    height: 54px;
    margin: 0 auto;
    border-radius: 20px;
    font-weight: 700;
    cursor: pointer;
}