.footer {
    /* background: black; */
    padding: 3rem 1rem 3rem 1rem;
    border-top: .5px solid rgba(255, 255, 255, 0.25);
}

.subscribe-section,
.socials-container {
    margin-bottom: 5rem;
}

.subscribe-title {
    letter-spacing: 1.5px;
    margin-bottom: 1rem;
}

form input {
    outline: none;
    height: 44px;
    width: 223px;
    border-radius: 0;
    border: 1px solid white;
    padding-left: 1rem;
}

.subscribe-button {
    height: 44px;
    width: 53px;
    background: transparent;
    color: white;
    border: 1px solid white;
    cursor: pointer;
}

.subscribe-button:hover {
    border: 1px solid rgb(255, 86, 86);
    color: rgb(255, 86, 86);
}

form input::placeholder {
    text-transform: uppercase;
}

.socials-container {
    display: flex;
    gap: 35px;
}

.social {
    font-size: 30px;
    cursor: pointer;
    transition: 100ms;
}

.social:hover {
    color: rgb(255, 86, 86);
}

.credits {
    font-size: 12px;
    font-weight: 300;
    opacity: .7;
}

@media screen and (min-width: 1024px){

    .footer-container {
        display: flex;
        max-width: 1200px;
        justify-content: space-between;
        margin: 0 auto;
    }

    .credits {
        display: flex;
        justify-content: center;
    }
}

@media screen and (min-width: 1230px){
    .footer {
        padding: 3rem 0;
    }
}

.external-link:hover {
    color:rgb(255, 86, 86);
    text-decoration: underline;
}