
:root {
  
  --desktop-width: 1024px;
  --tablet-width: 992px;
}

@import './header.module.css';
@import './shoes.module.css';
@import './footer.module.css';

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  max-width: 100vw;
  color: #ffffff;
}

.is-fixed {
  overflow: hidden;
}

@media screen and (min-width: 1024px){
  
  .is-fixed {
    overflow: auto;
  }
}

::selection {
  color: rgb(255, 86, 86);
}

img {
  display: block;
}

body {
  color: rgb(var(--foreground-rgb));
    font-family: 'poppins', sans-serif;
    background-image: url(../images/pattern.webp);
}

a {
  color: inherit;
  text-decoration: none;
}

@media (prefers-color-scheme: dark) {
  html {
    color-scheme: dark;
  }
}

li {
  list-style: none;
}

ul {
  padding: 0;
}

.button-to-top {
  width: 50px;
  height: 50px;
  border: .5px solid rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  font-size: 13px;
  cursor: pointer;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  z-index: 130;
  transition: 300ms;
  opacity: 0;
  pointer-events: none;
}

.button-to-top.showBtn {
  pointer-events: all;
  opacity: 1;
}

.button-to-top:hover {
    color: rgb(255, 86, 86);
    border: .5px solid rgb(255, 86, 86);
}