.main {
    padding: 0 1rem;
    display: flex;
    flex-direction: column;
    row-gap: 6rem;
    margin-bottom: 8rem;
}

.shoes-item {
    width: 100%;
    position: relative;
}

.shoes-item::after {
    content: '';
    width: 10rem;
    background: white;
    height: .5px;
    position: absolute;
    bottom: -3rem;
    left: 50%;
    transform: translateX(-50%);
    opacity: .5;
}

.shoes-year {
    font-size: 140px;
    font-weight: 900;
    background: linear-gradient(131.82deg, #727272 0%, #1111115e 98.11%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    letter-spacing: 8px;
    position: absolute;
    top: -2rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
}

.shoes-number {
    font-size: 50px;
    letter-spacing: normal;
    position: absolute;
    top: 4rem;
    right: 2rem;
    background: linear-gradient(150.82deg, #727272 0%, #1111115e 98.11%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    opacity: 1;
    display: none;
}

.shoes-title {
    font-size: 40px;
    text-transform: uppercase;
    margin-bottom: 1rem
}

.shoes-image {
    max-width: 230px;
    margin: 0 auto;
}

.shoes-description {
    font-size: 1rem;
    font-weight: 300;
    margin-bottom: 1rem;
}

.shoes-author {
    font-size: 11px;
    margin-bottom: 25px;
}

.shoes-button {
    font-size: 14px;
    font-weight: 400;
    border: 1px solid white;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 46px;
    cursor: pointer;
    transition: 100ms;
}

.shoes-button:hover {
    color: rgb(255, 86, 86);
    border: 1px solid rgb(255, 86, 86);
}

.divider {
    display: none;
}

@media screen and (min-width: 1024px){

    .main {
        max-width: 1100px;
        margin: 0 auto;
        row-gap: 0;
    }

    .shoes-item {
        display: flex;
        align-items: center;
        margin: 0 auto;
        height: 100vh;
    }

    .shoes-image {
        max-width: 400px;
    }

    .shoes-year {
        font-size: 400px;
        top: 50%;
        transform: translateY(-50%) translateX(-50%);
    }

    .shoes-item:nth-child(even){
        flex-direction: row-reverse;
    }

    .shoes-item:nth-child(even) .shoes-year{
        background: linear-gradient(221.82deg, #727272 0%, #1111115e 98.11%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .shoes-item:nth-child(even) .shoes-number{
        background: linear-gradient(185.82deg, #727272 0%, #1111115e 98.11%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        left: 0;
    }

    .shoes-item::after {
        display: none;
    }

    .shoes-info {
        max-width: 50%;
    }

    .shoes-more-info {
        display: flex;
        gap: 4rem;
        align-items: center;
    }

    .shoes-author {
        margin-bottom: 0;
    }

    .divider {
        display: block;
        height: 30px;
        width: 1px;
        background: white;
        display: inline-block;
    }

    .shoes-button {
        width: 96px;
    }

    .shoes-description {
        font-size: 20px;
        margin-bottom: 2rem;
    }

    .shoes-number {
        display: block;
    }
}